#root{
    width: 100vw;
    height: 100vh;
}
*{
    box-sizing: border-box;
}
body{
    margin:0;
    width: 100vw;
    height: 100%;
}
.app {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.home{

    flex-grow: 1; 
}
.property-card{
    display: flex;
    flex-grow: 1;
    flex-direction: column;
}

.property-name{
    margin-left: 2rem;
    margin-right: 2rem;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #C63C51;
}
.property-name h2{
    padding-left: 1rem;
    padding-Right: 1rem;
    padding-bottom: 0;
    font-family: "Helvetica", sans-serif;
    font-style: normal;
}

.nav ul{
    padding: 0;
    margin:0;
    list-style: none;
    display: flex;
    gap: 1rem;
}
.nav a{
    color: aliceblue;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
}

.nav li.active{
    background-color: #522258;
}
.nav li:hover{
    background-color: #8C3061;
}

.site-title{
    font-size: 2rem;
}