.nav{
    background-color: #C63C51;
    color:aliceblue;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; 
    align-items: stretch;
    gap: 2rem;
    padding: 0 1rem;
    font-family: "Lobster", sans-serif;
    font-weight: 200;
    font-style: normal;
    font-size: 1.5rem;
}
.logo-container{
    display: flex;
    align-items: center;
}
.App-logo {
    height: 2.5rem;
    padding: 0.1rem; 
    width: auto;
  }
@media (max-width: 600px) {
    .nav {
      font-size: 1rem;
      gap: 1rem;
      padding: 0 0.5rem;
    }
  }
  