.apartment-info-container{
    display: flex;
    align-items: center;
    padding: 1rem 2rem 0rem 2rem;
}
.apartment-info {
    border: 1px solid #C63C51;
    padding: 20px;
    width: 300px;
    
  }
  
  .apartment-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    font-family: Arial, sans-serif;
    align-items: center;
  }
  
  .apartment-size {
    font-weight: bold;
    font-size: 24px;
  }
  
  .apartment-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .apartment-table td {
    padding: 8px;
    border-bottom: 1px solid #ddd;
    font-family: Arial, sans-serif;
  }
  
  .apartment-table a {
    text-decoration: none;
    color: #007bff;
  }
  
  .apartment-table a:hover {
    text-decoration: underline;
  }
  