.infoDiv {
    position: fixed;
    background: rgba(0, 0, 0, 0.75);
    color: white;
    padding: 8px 12px;
    border-radius: 8px;
    pointer-events: none;
    font-size: 0.9rem;
    z-index: 10;
    max-width: 200px;
    box-shadow: 0 4px 10px rgba(0,0,0,0.3);
    transition: opacity 0.2s ease;
  }
  .showBox {
    padding: 2rem;
    
    align-items: center;
    
    display: flex;
    flex-direction: row;
    align-items: stretch;
    width: 100%;
    height: 600px;
    position: relative;
  
  
  }
  .property-name{
    flex-wrap: wrap; 
  }
  .canvas-container{
    
    background: #8be1fe;
    border-radius: 8px;
  }
  .modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .modal {
    background: white;
    padding: 2rem;
    border-radius: 12px;
    max-width: 500px;
    width: 90%;
    box-shadow: 0 10px 30px rgba(0,0,0,0.3);
    z-index: 101;
  }
  
  .modal h2 {
    margin-top: 0;
  }
  
  .modal button {
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background: #222;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
  }
  .close-btn {
    position: absolute;
    top: 8px;
    right: 12px;
    background: none;
    border: none;
    font-size: 1.5rem;
    line-height: 1;
    cursor: pointer;
    color: #444;
  }
  .canvas-container {
    flex: 1;
    height: 100%;
    transition: all 0.3s ease;
    min-width: 0;
  }
  
  .canvas-container.full-width {
    width: 100%;
  }
  .info-panel {
    border-radius: 8px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    padding: 2rem;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
    z-index: 20;
    overflow-y: auto;
  }
  .info-panel {
    transition: opacity 0.3s ease;
    opacity: 1;
  }
  .info-panel {
    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .floorplan-img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 8px;
    display: block;
  }
  
     
  